import {Component, OnInit} from '@angular/core';
import {ProductsService} from "./products/products.service";
import {OrdersService} from "./orders/orders.service";
import {RouteHistoryService} from "./shared/route-history.service";
import {AuthService} from "./auth/auth.service";
import {MessageService} from "./shared/message.service";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{


  constructor(private productsService: ProductsService , private ordersService: OrdersService, routeHistory: RouteHistoryService,
  private auth: AuthService){
     this.productsService.prodLoaded.subscribe((prod) => {

     this.productsService.setProducts(prod);
    });

  }

  ngOnInit() {
    this.productsService.loadProducts();
 }
}




