import { Component, OnInit } from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {ValidateFn} from "codelyzer/walkerFactory/walkerFn";
import {Observable} from "rxjs/Observable";
import {AuthService} from "../auth.service";
import {User} from "../user.model";

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  registerForm: FormGroup;
  constructor(private auth: AuthService) { }

  ngOnInit() {
    this.registerForm = new FormGroup({
      'email' : new FormControl(null, [Validators.required , Validators.email] ),
      'name' : new FormControl(null),
      'surname' : new FormControl(null),
      'address' : new FormControl(null),
      'phone' : new FormControl(null),
      'password' : new FormControl(null, [ Validators.required, Validators.minLength(8)  ]),
      'password1' : new FormControl(null, [ Validators.required, Validators.minLength(8) ]),
    });

  }
 /* passwordValidator(control: FormGroup): {[s: string]: boolean} {

    if(control.get('password').value !== control.get('password1').value){
      return {'passwordMustMatch' : true };
    }
    return null;
  }
  emailValidator(control: FormControl): Promise<any> | Observable<any>{

    return this.auth.checkMail(control.value);
  }*/
 onSubmit() {
   console.log('called');

   const user = new User(null , this.registerForm.get('email').value , this.registerForm.get('name').value , this.registerForm.get('surname').value , this.registerForm.get('address').value ,
      this.registerForm.get('phone').value , this.registerForm.get('password').value , null);
   this.auth.register(user);
 }
}



