import { Component, OnInit } from '@angular/core';
import {DataService} from "../shared/data.service";
import {OrdersService} from "../orders/orders.service";
import {Subscription} from "rxjs/Subscription";
import {myOrder} from "../orders/myorder.model";

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {
  myOrd;
  orders: myOrder[];
  sub: Subscription;
  constructor(private dataService: DataService , private ordersService: OrdersService) { }

  ngOnInit() {
    this.myOrd = this.ordersService.order;
    this.ordersService.loadOrders();
    this.sub = this.ordersService.ordLoaded.subscribe((orders) => {
      this.orders = orders;
    });
  }

}



