import {Component, Input, OnInit} from '@angular/core';
import {myOrder} from "../myorder.model";
import {OrdersService} from "../orders.service";
import {Router} from "@angular/router";
import {AuthService} from "../../auth/auth.service";
import {MessageService} from "../../shared/message.service";

@Component({
  selector: 'app-order-item',
  templateUrl: './order-item.component.html',
  styleUrls: ['./order-item.component.css']
})
export class OrderItemComponent implements OnInit {
  @Input() order: myOrder;
  constructor(private orderService: OrdersService ,private router: Router , private auth: AuthService ,private mexService: MessageService) { }

  ngOnInit() {
  }
  onCancelOrder(){

    this.orderService.cancelOrder();
  }
  onSaveOrder(){
    if(this.auth.isLogged()) {
      this.orderService.saveOrder();
    }
    else{
      this.mexService.add('you must login to store order' ,'err',3000);
      this.router.navigate(['/login']);
    }
  }
}



