import {Component, Input, OnInit} from '@angular/core';
import {Product} from "../product.model";
import {OrdersService} from "../../orders/orders.service";

@Component({
  selector: 'app-item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.css']
})
export class ItemComponent implements OnInit {
  @Input() item: Product;
  quantity = 0;
  constructor(private ordersService: OrdersService) { }

  ngOnInit() {
    this.quantity = this.ordersService.getQuantityById(this.item._id);
    console.log(this.quantity);
  }
  addProduct() {
    this.ordersService.updateProducts(this.item);


    this.quantity++;
    console.log(this.ordersService.order.products);


  }
  removeProduct() {
    this.ordersService.deleteProduct(this.item);
    this.quantity--;
    console.log(this.ordersService.order.products);
  }
}



