import { Injectable } from '@angular/core';
import {Router} from '@angular/router';
import {HttpClient, HttpHeaders, HttpResponse} from '@angular/common/http';
import {User} from './user.model';
import {MessageService} from "../shared/message.service";
import {RouteHistoryService} from "../shared/route-history.service";
import {Observable} from "rxjs/Observable";


@Injectable()
export class AuthService {


  user: User = null;

  url: string = './users/';

  constructor(private router: Router , private http: HttpClient, private messageService: MessageService ,
              private routerHistory: RouteHistoryService) {

  }

  isLogged(){
    return this.user != null;
  }

  login(email: string , password: string){
    this.http.post <HttpResponse<any>>(this.url + 'login',{'email': email, 'password': password},
      {
        observe: 'response'

      }
    ).subscribe((response: HttpResponse<any>)  => {

      console.log(response.body);
      this.user = new User(response.body.user._id, response.body.user.email, response.body.user.name, response.body.user.surname ,
        response.body.user.address , response.body.user.phone, null , response.body.token);
      console.log(this.user);
      if(this.routerHistory.lastUrl.length !== 0 && this.routerHistory.lastUrl[this.routerHistory.lastUrl.length-1] !== '/login' ) {
        this.router.navigate([this.routerHistory.lastUrl[this.routerHistory.lastUrl.length - 1]]);
      } else {
        this.router.navigate(['/products']);
      }
      },(error) =>{
      this.messageService.add('login failed' + error.toString(), 'err', 3000);
       this.user = null;

      }

    );
  }
  logOut() {
    this.user = null;
    this.http.post<HttpResponse<any>>(this.url + 'logout',{}).subscribe(() => {
      this.messageService.add('logged out successfully' , 'succ', 3000);
    } , (error) => {
      this.messageService.add('logout failed' + error.toString(), 'err', 3000);
    });
  }
  register(user: User ) {
     this.http.post<HttpResponse<any>>(this.url + 'register', user , {
      observe: 'response'
    }).subscribe( (response: HttpResponse<any>) => {
      this.user = new User(response.body.user._id, response.body.user.email, response.body.user.name, response.body.user.surname ,
        response.body.user.address , response.body.user.phone, null , response.body.token);
      this.messageService.add('succefully registered', 'succ', 3000);
      this.router.navigate(['/products']);
    },(error) => {
      this.messageService.add('registration failed' + error.toString(), 'err', 3000);
      this.user = null;
    });
  }
  checkMail(email: string): Observable<any> {

     return this.http.post(this.url + 'checkemail',{'email': email} ,{
      observe: 'response'
    }).map((val) => {
       if (val === null){

         console.log('null result');
         return null;
       } else {
         console.log(val);
         return null;
       }
     });
  }

}



