import { Injectable } from '@angular/core';
import {Product} from '../products/product.model';
import {myOrder} from './myorder.model';
import {DataService} from "../shared/data.service";
import {MessageService} from "../shared/message.service";
import {Router} from "@angular/router";
import {Subject} from "rxjs/Subject";



@Injectable()
export class OrdersService {

  order: myOrder;
  myOrders: myOrder[];
  ordLoaded = new Subject<myOrder[]>();
  constructor(private dataService: DataService, private messageService: MessageService,private router: Router) {
    this.order = new myOrder(1 , 1 , 0 , Date.now() , [] );
  }

  setPersons(persNum: number) {
    this.order.persons = persNum;
  }
  getPersons(): number {
    return this.order.persons;
  }

  updateTotal(val: number) {
    this.order.total += val;
  }

  updateProducts(prod: Product) {
    console.log(prod._id);
    if (this.order.products.length === 0) {

      this.order.products.push({product: prod, quantity: 1}) ;
    } else {

      const ind = this.findById( prod._id);
       console.log('ind = to ', ind );
       if (ind !== -1) {
         this.order.products[ind].quantity += 1;
       } else {
         this.order.products.push({product: prod, quantity: 1}) ;
       }

    }
    this.updateTotal(prod.price);
  }
  deleteProduct(prod: Product) {
    console.log('deleting product');
    if (this.order.products.length === 0) {
      return;
    } else {

      const ind = this.findById(prod._id);
      console.log('index  = ',ind )
      if (ind !== -1) {
        if (this.order.products[ind].quantity === 1) {
          this.order.products.splice(ind, 1);
        } else {
          this.order.products[ind].quantity -= 1;
        }
        this.updateTotal(-prod.price);
      }
    }
  }
  getQuantityById(id: string) : number {
    if(!(this.order.products.length === 0)) {
      const ind = this.order.products.findIndex((p) => {
        console.log(id);
        return p.product._id === id;

      });
      if ( ind != -1) {
        return this.order.products[ind].quantity;
      }

    }
    return 0;

  }
  findById(  id: string) : number {
    const ind = this.order.products.findIndex((p) => {

      return p.product._id === id;

    });

    return ind;
  }
  cancelOrder(){
    this.order = new myOrder(1 , 1 , 0 , Date.now() , [] );
  }
  saveOrder(){
    this.dataService.saveOrder(this.order).subscribe((order) =>{
      this.messageService.add('order saved' , 'succ', 3000);
      if(order){
        this.cancelOrder();
        this.router.navigate(['/checkout']);
      }
    } , (error) =>{
      this.messageService.add('saving order error :' + error.toString(), 'err', 3000);

    });
  }
  loadOrders(){
    this.dataService.loadOrders().subscribe((orders) => {

      this.myOrders = orders;
      this.ordLoaded.next(this.myOrders);
      console.log(this.myOrders);

    },(err) => {
      this.messageService.add('getting order error :' + err.toString(), 'err', 3000);
    });
  }
}



