import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {OrdersService} from '../orders/orders.service';
import {AuthService} from "../auth/auth.service";


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  persons = 1 ;
  sideOpen=false;
  constructor(private orderService: OrdersService,private auth: AuthService) { }

  ngOnInit() {
    this.persons = this.orderService.getPersons();
  }
 onAddPerson(val: number ) {
    if(this.persons > 1 || val > 0) {
      this.orderService.setPersons(this.orderService.getPersons() + val);
      this.persons = this.persons + val;
    }

    }
    onSidenav(){
     this.sideOpen = !this.sideOpen;
    }
}



